<template>
  <div class="login">
    <div class="container pt-5">
      <div class="row justify-content-center text-center mb-2 mx-auto p-3 login-frame">
        <div class="col-12 mb-3 h3 text-orange font-weight-bold">
          潔旗雞排
        </div>
        <div class="col-12 mb-3">
          <img src="https://uupon.tw/resources/front/zh/images/logo.svg" alt="" width="65%">
        </div>
        <div class="col-12 mb-3 text-gray font-weight-bold text-left">
          店家帳號 00000000
        </div>
        <div class="col-12 mb-3 text-gray font-weight-bold text-left">
          <p class="mb-1">使用者帳號</p>
          <input type="text" placeholder="請輸入使用者帳號" v-model="username" class="w-100">
        </div>
        <div class="col-12 mb-5 text-gray font-weight-bold text-left">
          <p class="mb-1">店家密碼</p>
          <input type="password" placeholder="請輸入密碼" v-model="password" class="w-100">
        </div>
        <div class="col-12">
          <button class="btn-submit py-2 h5 w-100 font-weight-bold text-white" @click="submit">送出</button>
        </div>
      </div>
    </div>
    <div class="model-frame d-flex align-items-center justify-content-center" v-if="noticeFrame">
      <div class="model p-4">
        <p class="h4 font-weight-bold text-center my-4">{{ msg }}</p>
        <div class="d-flex align-items-center justify-content-center">
          <button class="cancel-btn h5 py-2 w-50" @click="noticeFrame = false, msg = ''">確認</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'login',
  data () {
    return {
      username: '',
      password: '',
      number: '',
      noticeFrame: false,
      msg: ''
    }
  },
  created () {
  },
  methods: {
    submit () {
      console.log(this.username + '123', this.password)
      if (this.username + '123' === this.password) {
        this.$router.push({path: '/main', query: {user: this.username}})
      } else if (this.username === '' || this.password === '') {
        this.msg = '請輸入帳號及密碼'
        this.noticeFrame = true
      } else {
        this.msg = '帳號或密碼輸入錯誤'
        this.noticeFrame = true
      }
    }
  }
}
</script>

<style scoped>
.login{
  background: #dbdbdb;
  width: 100%;
  height: 100vh;
}
.login input{
  height: 40px;
  border-radius: 12px;
  border: 2px solid #f6aa00;
  padding: 6px;
  font-size: 18px;
  color: #888888;
  /* outline: blue; */
}
.login input:focus,.login input:active{
  height: 40px;
  border-radius: 12px;
  /* border: 3px solid #f6aa00; */
  padding: 6px;
  font-size: 18px;
  outline: none;
  box-shadow: 0px 0px 8px 1px rgba(0,0,0, 0.3);
}
.login-frame{
  /* border: 2px solid #f6aa00; */
  border-radius: 8px;
  /* width: 90%; */
  max-width: 500px;
  background: #ffffff;
  box-shadow: 0px 0px 8px 3px rgba(0, 0, 0, 0.2);
}
.btn-submit{
  background: #f6aa00;
  border: none;
  outline: none;
}
.btn-submit:hover{
  color: #858585 !important;
}
.btn-submit:active,.btn-submit:focus{
  border: none;
  outline: none;
}
</style>



