<template>
  <div class="home pt-5 px-2">
    <div class="content px-lg-5 px-3 py-4 mx-auto">
      <div class="container">
        <div class="row">
          <div class="col">
            <p class="h3 text-orange font-weight-bold text-center">潔旗雞排 {{ this.username }} 您好</p>
          </div>
        </div>
        <div class="row">
          <button class="col-6 tab-btn h5 py-2 font-weight-bold" @click="pointType = 'operate'" :class="{'tab-btn-checked':pointType === 'operate'}">點數操作</button>
          <button class="col-6 tab-btn h5 py-2 font-weight-bold" @click="pointType = 'record'" :class="{'tab-btn-checked':pointType === 'record'}">點數紀錄</button>
        </div>
        <div class="row pt-3" v-if="pointType === 'operate'">
          <div class="col px-0">
            <div class="w-100 d-flex justify-content-start">
              <button @click="operateType = 'add'" class="w-25 pointType-btn h5 py-2 mb-0" :class="{'pointType-btn-checked':operateType === 'add'}">累點</button>
              <button @click="operateType = 'sub'" class="w-25 pointType-btn h5 py-2 mb-0" :class="{'pointType-btn-checked':operateType === 'sub'}">兌點</button>
            </div>
          </div>
          <!-- 累點鍵盤 -->
          <div class="col-12 bg-light-gray py-3" v-if="operateType === 'add'">
            <div class="mb-3 text-gray">手機號碼 <div class="mt-1 input-btn text-center" @click="enterType('addphone')" :class="{'input-btn-border': enternum  === 'addphone'}">{{ addphone }}</div></div>
            <div class="mb-3 text-gray">本次消費金額 <div  class="mt-1 input-btn text-center" @click="enterType('addmoney')" :class="{'input-btn-border': enternum === 'addmoney'}">{{ addmoney }}元</div></div>
            <div class="text-gray">本次累點 {{ addpoint }}</div>
          </div>
          <div class="col-12 keyboard-frame bg-light-gray pb-3" v-if="operateType === 'add'">
            <div class="d-flex justify-content-center align-items-center">
              <div class="keyboard-btn" @click="enterNum(7)">7</div>
              <div class="keyboard-btn" @click="enterNum(8)">8</div>
              <div class="keyboard-btn" @click="enterNum(9)">9</div>
            </div>
            <div class="d-flex justify-content-center align-items-center">
              <div class="keyboard-btn" @click="enterNum(4)">4</div>
              <div class="keyboard-btn" @click="enterNum(5)">5</div>
              <div class="keyboard-btn" @click="enterNum(6)">6</div>
            </div>
            <div class="d-flex justify-content-center align-items-center">
              <div class="keyboard-btn" @click="enterNum(1)">1</div>
              <div class="keyboard-btn" @click="enterNum(2)">2</div>
              <div class="keyboard-btn" @click="enterNum(3)">3</div>
            </div>
            <div class="d-flex justify-content-center align-items-center">
              <div class="keyboard-btn" @click="clearNum()">清空</div>
              <div class="keyboard-btn" @click="enterNum(0)">0</div>
              <div class="keyboard-btn" @click="submit()">執行</div>
            </div>
          </div>
          <!-- 兌點鍵盤 -->
          <div class="col-12 bg-light-gray py-3" v-if="operateType === 'sub'">
            <div class="mb-3 text-gray">手機號碼 <div class="mt-1 input-btn text-center" @click="enterType('subphone')" :class="{'input-btn-border': enternum  === 'subphone'}">{{ subphone }}</div></div>
            <div class="mb-3 text-gray">本次折抵點數 <div  class="mt-1 input-btn text-center" @click="enterType('subpoint')" :class="{'input-btn-border': enternum === 'subpoint'}">{{ subpoint }}</div></div>
          </div>
          <div class="col-12 keyboard-frame bg-light-gray pb-3" v-if="operateType === 'sub'">
            <div class="d-flex justify-content-center align-items-center">
              <div class="keyboard-btn" @click="enterNum(7)">7</div>
              <div class="keyboard-btn" @click="enterNum(8)">8</div>
              <div class="keyboard-btn" @click="enterNum(9)">9</div>
            </div>
            <div class="d-flex justify-content-center align-items-center">
              <div class="keyboard-btn" @click="enterNum(4)">4</div>
              <div class="keyboard-btn" @click="enterNum(5)">5</div>
              <div class="keyboard-btn" @click="enterNum(6)">6</div>
            </div>
            <div class="d-flex justify-content-center align-items-center">
              <div class="keyboard-btn" @click="enterNum(1)">1</div>
              <div class="keyboard-btn" @click="enterNum(2)">2</div>
              <div class="keyboard-btn" @click="enterNum(3)">3</div>
            </div>
            <div class="d-flex justify-content-center align-items-center">
              <div class="keyboard-btn" @click="clearNum()">清空</div>
              <div class="keyboard-btn" @click="enterNum(0)">0</div>
              <div class="keyboard-btn" @click="submit()">執行</div>
            </div>
          </div>
        </div>
        <div class="row mt-3" v-if="pointType === 'record'">
          <div class="col px-0">
            <div class="d-flex justify-content-start">
              <button @click="recordType = 'cancel'" class="pointType-btn h5 py-2 px-3 mb-0" :class="{'pointType-btn-checked':recordType = 'cancel'}">取消訂單</button>
            </div>
          </div>
          <div class="col-12 bg-light-gray py-3">
            <p class="h5 text-gray text-right">{{ today }}</p>
            <div class="table-frame">
              <table class="table">
                <thead class="bg-orange text-white text-center">
                  <tr>
                    <th scope="col" class="text-nowrap">功能</th>
                    <th scope="col" class="text-nowrap">時間</th>
                    <th scope="col" class="text-nowrap">手機號碼</th>
                    <th scope="col" class="text-nowrap">累</th>
                    <th scope="col" class="text-nowrap">兌</th>
                    <th scope="col" class="text-nowrap">金額</th>
                  </tr>
                </thead>
                <tbody class="bg-white text-center">
                  <tr v-for="(item, index) in recordList" :key="index">
                    <th scope="row">
                      <button class="cancel-btn h6 mb-0" v-if="item.func === 0" @click="cancel(index)">取消</button>
                      <button class="cancel-btn h6 mb-0" disabled v-if="item.func === 1">已取消</button>
                    </th>
                    <td>{{ item.time }}</td>
                    <td>{{ item.phone }}</td>
                    <td>{{ item.add }}</td>
                    <td>{{ item.sub }}</td>
                    <td>{{ item.money }}</td>
                  </tr>
                  <tr v-if="recordList.length < 1">
                    <td colspan="6">目前無紀錄</td>
                  </tr>
                </tbody>
              </table>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="model-frame d-flex align-items-center justify-content-center" v-if="noticeFrame">
      <div class="model p-4">
        <p class="h4 font-weight-bold text-center my-4">{{ msg }}</p>
        <div class="d-flex align-items-center justify-content-center">
          <button class="cancel-btn h5 py-2 w-50" @click="noticeFrame = false, msg = ''">確認</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import data from '@/assets/data.json'
export default {
  name: 'home',
  data () {
    return {
      username: '',
      pointType: 'operate',
      addphone: '',
      addmoney: 0,
      addpoint: 0,
      subphone: '',
      subpoint: 0,
      operateType: 'add',
      recordType: 'cancel',
      enternum: '',
      today: new Date().getFullYear() + '/' + (new Date().getMonth() + 1) + '/' + new Date().getDate(),
      recordList: [],
      noticeFrame: false,
      msg: ''
    }
  },
  created () {
    console.log(this.$route, data)
    this.username = this.$route.query.user
  },
  watch: {
    operateType () {
      this.enternum = ''
    },
    addmoney () {
      this.addpoint = parseInt(Number(this.addmoney) / 100)
    }
  },
  methods: {
    submit () {
      console.log(this.operateType)
      if (this.operateType === 'add') {
        if (this.addphone.length < 10 || this.addmoney < 1) {
          this.msg = '請輸入完整手機號碼及金額'
          this.noticeFrame = true
        } else {
          this.recordList.unshift({ func: 0, time: new Date().getHours() + ':' + (Number(new Date().getMinutes()) < 10 ? `0${new Date().getMinutes()}` : new Date().getMinutes()), phone: this.addphone, add: parseInt(Number(this.addmoney) / 100), sub: 0, money: this.addmoney })
          this.msg = '累點成功'
          this.noticeFrame = true
          this.addphone = ''
          this.addmoney = 0
        }
      } else if (this.operateType === 'sub') {
        if (this.subphone.length < 10 || this.subpoint < 1) {
          this.msg = '請輸入完整手機號碼及點數'
          this.noticeFrame = true
        } else {
          this.recordList.unshift({ func: 0, time: new Date().getHours() + ':' + (Number(new Date().getMinutes()) < 10 ? `0${new Date().getMinutes()}` : new Date().getMinutes()), phone: this.subphone, add: 0, sub: this.subpoint, money: 0 })
          this.msg = '兌點成功'
          this.noticeFrame = true
          this.subphone = ''
          this.subpoint = 0
        }
      }
    },
    enterNum (num) {
      switch (this.enternum) {
        case 'addphone':
          if (this.addphone.length > 9) return
          this.addphone = this.addphone + num
          break
        case 'addmoney':
          this.addmoney = Number(String(this.addmoney) + num)
          break
        case 'subphone':
          if (this.subphone.length > 9) return
          this.subphone = this.subphone + num
          break
        case 'subpoint':
          this.subpoint = Number(String(this.subpoint) + num)
          break
        default:
      }
    },
    clearNum () {
      console.log(this.enternum)
      switch (this.enternum) {
        case 'addphone':
          this.addphone = ''
          break
        case 'addmoney':
          this.addmoney = 0
          break
        case 'subphone':
          if (this.subphone.length > 10) return
          this.subphone = ''
          break
        case 'subpoint':
          this.subpoint = 0
          break
        default:
      }
    },
    enterType (item) {
      this.enternum = item
      console.log(this.enternum)
    },
    cancel (index) {
      this.recordList[index].func = 1
    }
  }
}
</script>

<style>
  .home{
    background: #dbdbdb;
    width: 100%;
    height: 100vh;
  }
  .content{
    border-radius: 8px;
    /* width: 90%; */
    max-width: 900px;
    background: #ffffff;
    box-shadow: 0px 0px 8px 3px rgba(0, 0, 0, 0.2);
  }
  .tab-btn,.cancel-btn{
    border: none;
    color: #ffa600;
  }
  .tab-btn-checked,.tab-btn:focus,.tab-btn:active,.cancel-btn:focus,.cancel-btn:active{
    border: none;
    outline: none;
    background: #ffa600;
    color: #ffffff;
  }
  .pointType-btn{
    border: none;
    background: #dbdbdb;
    color: #7c7c7c;
    font-weight: 900;
  }
  .pointType-btn-checked,.pointType-btn:focus,.pointType-btn:active{
    border: none;
    outline: none;
    background: #efefef;
    color: #ffa600;
  }
  .keyboard-btn{
    width: calc(33% - 2px);
    text-align: center;
    background: rgb(255, 255, 255);
    border: 1px solid #ffa600;
    border-radius: 8px;
    margin: 2px;
    color: #ffa600;
    cursor: pointer;
    padding: 8px;
    font-size: 24px;
    font-weight: 800;
  }
  .keyboard-frame div:nth-child(4) .keyboard-btn:nth-child(3){
    color: #ffffff;
    background: rgb(255, 166, 0);
  }
  .keyboard-frame div:nth-child(4) .keyboard-btn:nth-child(1){
    color: #ffffff;
    background: #c0c0c0;
    border: 2px solid #c0c0c0;
  }
  .input-btn,.input-btn:focus,.input-btn:active{
    width: 100%;
    height: 40px;
    border-radius: 12px;
    border: 2px solid #c0c0c0;
    padding: 6px;
    font-size: 18px;
    color: #888888;
    background: #ffffff;
  }
    .input-btn-border,.input-btn-border:focus,.input-btn-border:active {
      height: 40px;
      border-radius: 12px;
      padding: 6px;
      font-size: 18px;
      border: 2px solid #f6aa00;
      outline: none;
      box-shadow: 0px 0px 8px 1px rgba(0,0,0, 0.3);
    }
  .cancel-btn{
    background: rgb(255, 166, 0);
    color: #ffffff;
    font-weight: 800;
    border-radius: 8px;
    padding: 4px 8px;
  }
  .cancel-btn:disabled{
    background: rgb(173, 173, 173);
  }
  .table-frame{
    overflow-x: scroll;
  }
  .table tbody td, .table tbody th {
    vertical-align: middle;
    padding: 8px 4px;
  }
</style>



